.gallery {
  position: relative;
  &-img {
    position: absolute;
    top: -48px;
    left: 50%;
    transform: translate(-50%, 0);

    width: 100%;

    @media screen and (min-width: 768px) {
      top: -106px;
    }

    @media screen and (min-width: 768px) {
      top: -196px;
    }
  }
}
