@media screen and (min-width: 320px) {
  .footer {
    padding-top: 113px;
    background-color: #fff5f6;

    .title {
      display: block;
      margin: 0;

      width: 232px;
      font-family: Titan One;
      font-size: 18px;
      line-height: 1.166;

      text-transform: uppercase;
      color: $pink-subtitle;
    }
    .thumb-social {
      display: block;
      margin-top: 43px;
      margin-left: px;

      .social {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-bottom: 46px;
        margin-right: 0;
        margin-left: 147px;

        .social-item:not(:last-child) {
          margin-right: 28px;
        }
        .social-link {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 32px;
          height: 32px;
          background-color: #fea5bb;
          border-radius: 50%;

          .social-logo {
            display: block;
            width: 60%;
          }
        }
      }
    }
    .footer-hero {
      display: block;
      margin-top: 17px;
      margin-bottom: 0;
      margin-left: 0;
      margin-right: 0;

      max-width: 200px;
      font-family: Titan One;
      font-size: 34px;
      line-height: 1.147;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $main;
    }

    .contact-akcent {
      display: block;
      margin-bottom: 43px;
      margin-left: 0;
      margin-right: 0;

      font-family: DM Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 1.83;
      text-align: right;
      color: $main;

      .contact {
        color: $black;
        font-weight: bold;
        font-size: 12px;
        line-height: 1.83;
        text-align: right;
      }
    }
    .unicode-box {
      border-top: 1px solid #e1e1e1;

      .footer-unicode {
        display: block;
        margin-left: 79px;
        margin-right: 91px;
        padding-top: 25px;
        padding-bottom: 25px;

        min-width: 140px;
        font-weight: bold;
        font-size: 14px;
        line-height: 21px;
        text-align: center;
        color: $grey;
      }
    }
  }
}
@media screen and (min-width: 768px) {
  .footer {
    padding-top: 165px;
    .thumb-footer {
      display: flex;
      justify-content: space-between;

      .title {
        margin-top: 7px;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: auto;

        min-width: 340px;
        font-size: 26px;
        line-height: 1.153;
        letter-spacing: 0.04em;
      }
      .thumb-social {
        margin-top: 0;
        margin-right: 0;

        .social {
          margin-bottom: 46px;
          margin-right: 0;
          margin-left: auto;

          .social-link {
            min-width: 34px;
            min-height: 34px;
          }

          .social-item:not(:last-child) {
            margin-right: 30px;
          }
        }
      }
      .footer-hero {
        margin-top: 16px;
        margin-bottom: 0;
        margin-left: 0;
        margin-right: auto;

        max-width: 340px;
        max-height: 110px;
        font-size: 48px;
        line-height: 1.145;
      }
      .contact-akcent {
        margin: 0;

        min-width: 268px;
        font-size: 14px;
        line-height: 1.857;

        .contact {
          font-size: 14px;
          line-height: 1.857;
        }
      }
    }
    .unicode-box {
      margin-top: 91px;

      .footer-unicode {
        display: block;
        margin: 0 auto;
        padding-top: 29px;
        padding-bottom: 18px;

        min-width: 276px;
        font-size: 14px;
        line-height: 1.857;
      }
    }
  }
  @media screen and (min-width: 1200px) {
    .footer {
      .thumb-footer {
        display: flex;
        justify-content: space-between;

        .title {
          margin-top: 0px;
          margin-bottom: 0;
          margin-right: auto;
          margin-left: 114px;

          min-width: 472px;
          font-size: 30px;
          line-height: 1.133;
          letter-spacing: 0.04em;
        }
        .thumb-social {
          margin: 0;

          .social {
            margin-bottom: 50px;
            margin-right: 121px;
            margin-left: auto;

            .social-item:not(:last-child) {
              margin-right: 24px;
            }

            .social-link {
              min-width: 34px;
              min-height: 34px;
            }
          }
        }
        .footer-hero {
          margin-top: 15px;
          margin-bottom: 0;
          margin-right: auto;
          margin-left: 0;

          max-width: 474px;
          max-height: 66px;
          font-size: 58px;
          line-height: 1.137;
        }

        .contact-akcent {
          margin-bottom: 0;
          margin-right: 116px;
          min-width: 319px;

          font-size: 16px;
          line-height: 1.857;

          .contact {
            font-size: 16px;
            line-height: 1.857;
          }
        }
      }
      .unicode-box {
        margin-top: 97px;

        .footer-unicode {
          margin: 0 auto;
          padding-top: 9px;
          padding-bottom: 10px;

          min-width: 276px;
        }
      }
    }
  }
}
